import React from 'react';
import { Player } from 'video-react';
import Icon from './Icon';

const sources = [
  { id: 0, name: 'UseCases', video: 'https://meinsta.github.io/auth0-hpg/What_is_Auth0/01_Use_Cases.mp4' },
  { id: 1, name: 'Technology', video: 'https://meinsta.github.io/auth0-hpg/What_is_Auth0/02_Technology_1.mp4' },
  { id: 2, name: 'Deployment', video: 'https://meinsta.github.io/auth0-hpg/What_is_Auth0/03_Deployment.mp4' },
  { id: 3, name: 'Customization', video: 'https://meinsta.github.io/auth0-hpg/What_is_Auth0/04_Customization.mp4' },
  { id: 4, name: 'Result', video: 'https://meinsta.github.io/auth0-hpg/What_is_Auth0/05_Result.mp4' }
];

export default class Carousel extends React.Component {
	constructor (props) {
		super(props);
		
		this.state = {
			currentImageIndex: 0
		};
		
	    this.carouselHandler = this.carouselHandler.bind(this);
		this.nextSlide = this.nextSlide.bind(this);
		this.previousSlide = this.previousSlide.bind(this);
	}

	handleStateChange(state, prevState) {
		// copy player state to this component's state
		this.setState({
		  player: state
		});
	}

	play() {
		this.refs.player.play();
	}

	pause() {
		this.refs.player.pause();
	}

	load() {
		this.refs.player.load();
	}
	
	previousSlide () {
		const lastIndex = sources.length - 1;
		const { currentImageIndex } = this.state;
		const shouldResetIndex = currentImageIndex === 0;
		const index =  shouldResetIndex ? lastIndex : currentImageIndex - 1;
		
		this.setState({
			currentImageIndex: index
		});
	}
	
	nextSlide () {
		const lastIndex = sources.length - 1;
		const { currentImageIndex } = this.state;
		const shouldResetIndex = currentImageIndex === lastIndex;
		const index =  shouldResetIndex ? 0 : currentImageIndex + 1;

		this.setState({
			currentImageIndex: index
		});

		        this.refs.player.load();
	}

	componentDidMount() {

	    document.querySelector('.new-carousel video').addEventListener('ended', this.carouselHandler, false);
	}

	componentWillUnmount() {
	    document.querySelector('.new-carousel video').removeEventListener('ended', this.carouselHandler, false);
	}

	carouselHandler = (event) => {
		const lastIndex = sources.length - 1;
		const { currentImageIndex } = this.state;
		const index =  currentImageIndex + 1;

			if( !lastIndex )
			{
				 this.setState({
		          currentImageIndex: index,
		        });
		        document.querySelector('.new-carousel video').load();
		        document.querySelector('.new-carousel video').play();
			}
			if( lastIndex )
			{
				console.log('done!');
			}


	}

	render () {

		var elementPos = sources.map(function(x) {return x.id; }).indexOf(this.state.currentImageIndex);

		return (
			<div className="new-carousel">
				<Arrow direction="left" clickFunction={ this.previousSlide } glyph="&#9664;" />
				<ImageSlide url={ sources[elementPos].name } />
				<VideoSlide ref={ "player" } url={ sources[elementPos].video } />
				<Arrow direction="right" clickFunction={ this.nextSlide } glyph="&#9654;" />
			</div>
		);
	}
}

const Arrow = ({ direction, clickFunction, glyph }) => (
	<div 
		className={ `slide-arrow ${direction}` } 
		onClick={ clickFunction }>
		{ glyph } 
	</div>
);

const ImageSlide = ({ url }) => (
		<div><Icon icon={`${url}`} iconStyle={'image-slide'} /></div>
);

const VideoSlide = ({ ref, url }) => (
    <Player autoPlay muted>
      <source ref={`${ref}`} src={`${url}`} />
    </Player>
);



